<template>
  <div class="container">
    <div class="content">
      <div class="top">
        <div class="header">
          <img alt="logo" class="logo" src="../../assets/qdlogo.png" />
          <span class="title">vuesms</span>
        </div>
        <div class="desc">启点车险电销管理系统</div>
      </div>
      <div class="login">
        <!-- <Form @submit="onSubmit" :autoFormCreate="(form) => this.form = form"> -->
        <a-form @submit="onSubmit" :form="form">
  
          <!-- <a-alert type="error" :closable="true" @close="closeAlert" v-show="isShow" :message="error" showIcon style="margin-bottom: 24px;"/> -->
          <a-form-item>
            <a-input size="large" placeholder="admin" v-decorator="['name',{rules: [{ required: true, message: '请输入账户名', whitespace: true}]}]">
              <a-icon slot="prefix" type="user" />
            </a-input>
            <!-- <a-form-item
              fieldDecoratorId="name"
              :fieldDecoratorOptions="{rules: [{ required: true, message: '请输入账户名', whitespace: true}]}"
            >
              <a-input size="large" placeholder="admin" v-decorator="['name',{rules: [{ required: true, message: '请输入账户名', whitespace: true}]}]">
                <a-icon slot="prefix" type="user"/>
              </a-input> -->
  
          </a-form-item>
          <a-form-item>
            <a-input size="large" placeholder="888888" type="password" v-decorator="['password',{rules: [{ required: true, message: '请输入密码', whitespace: true}]}]">
              <a-icon slot="prefix" type="lock" />
            </a-input>
            <!-- <a-form-item
              fieldDecoratorId="password"
              :fieldDecoratorOptions="{rules: [{ required: true, message: '请输入密码', whitespace: true}]}"
            >
              <a-input size="large" placeholder="888888" type="password" v-decorator="['password',{rules: [{ required: true, message: '请输入密码', whitespace: true}]}]">
              <a-icon slot="prefix" type="lock"/>
              </a-input> -->
  
          </a-form-item>
  
          <!--<div>-->
          <!--<Checkbox :checked="true">自动登录</Checkbox>-->
          <!--<a style="float: right">忘记密码</a>-->
          <!--</div>-->
          <a-form-item>
            <a-button :loading="loginBtn" style="width: 100%;margin-top: 24px" size="large" htmlType="submit" type="primary">登录
            </a-button>
          </a-form-item>
          <!--<div>-->
          <!--其他登录方式-->
          <!--<a-icon class="icon" type="alipay-circle"/>-->
          <!--<a-icon class="icon" type="taobao-circle"/>-->
          <!--<a-icon class="icon" type="weibo-circle"/>-->
          <!--<router-link style="float: right" to="/dashboard/workplace">注册账户</router-link>-->
          <!--</div>-->
        </a-form>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import AForm from 'ant-design-vue/lib/form'
  import ATabs from 'ant-design-vue/lib/tabs'
  import AAlert from 'ant-design-vue/lib/alert'
  import AInput from 'ant-design-vue/lib/input'
  import AIcon from 'ant-design-vue/lib/icon'
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import ACheckbox from 'ant-design-vue/lib/checkbox'
  import AButton from 'ant-design-vue/lib/button'
  import Mock from '../../mock'
  import {
    mapActions
  } from 'vuex'
  const AFormItem = AForm.Item
  const ATabPane = ATabs.TabPane
  export default {
    name: 'Login',
    components: {
      AForm,
      ATabs,
      AAlert,
      AInput,
      AIcon,
      ARow,
      ACol,
      ACheckbox,
      AButton,
      AFormItem,
      ATabPane
    },
    data() {
      return {
        loginBtn: false,
        error: '',
        isShow: false
      }
    },
    beforeCreate() {
      this.form = this.$form.createForm(this)
    },
    computed: {
      //      systemName () {
      //        return this.$store.state.setting.systemName
      //      },
      //      linkList () {
      //        return this.$store.state.setting.footerLinks
      //      },
      //      copyright () {
      //        return this.$store.state.setting.copyright
      //      }
    },
    methods: {
      ...mapActions(['Login']),
      onSubmit(e) {
        e.preventDefault()
        let _this = this
        this.form.validateFields((err, values) => {
          if (!err) {
            _this.loginBtn = true
            _this.Login({
              username: _this.form.getFieldValue('name'),
              password: _this.form.getFieldValue('password')
            }).then((res) => {
              // sessionStorage.setItem('user',JSON.stringify(result))
              _this.loginSuccess()
              // _this.$router.options.routes[1].children.push(
              //   {
              //     name:'用户',
              //     path:'/user',
              //     icon:'user',
              //     components:() => import('@/pages/Settings/Users/Users.vue')
              //   }
              // )
              // debugger
              //  _this.$router.addRoutes(this.$router.options.routes);//调用addRoutes添加路由
            }).catch((err) => {
              // const result={ name: 'xiao', mobile: '123' }
              // sessionStorage.setItem('user',JSON.stringify(result))
              //  _this.$router.push('/dashboard/workplace')
              // _this.error = err.response.data.non_field_errors[0]
              // _this.isShow=true
              _this.requestFailed(err)
            });
          }
        })
      },
      timeFix() {
        const time = new Date()
        const hour = time.getHours()
        return hour < 9 ? '早上好' : (hour <= 11 ? '上午好' : (hour <= 13 ? '中午好' : (hour < 20 ? '下午好' : '晚上好')))
      },
      loginSuccess() {
        this.loginBtn = false
        this.$router.push('/dashboard/workplace')
        this.$notification.success({
          message: '欢迎',
          description: `${this.timeFix()}，欢迎回来`
        })
      },
      requestFailed(err) {
        this.$notification['error']({
          message: '错误',
          description: this.$myNotification.getExceptionInfo(err),
          duration: 4
        })
        this.loginBtn = false
      },
      // closeAlert(){
      //   this.isShow=false
      //   this.error=''
      // }
    }
  }
</script>

<style lang="less" scoped>
  .title {
    padding: 0;
  }
  
  @media (min-width: 768px) {
    .container {
      width: 100%
    }
  }
  
  .container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: auto;
    background: #f0f2f5 url('https://gw.alipayobjects.com/zos/rmsportal/TVYTbAXWheQpRcWDaDMu.svg') no-repeat center 110px;
    background-size: 100%;
    .content {
      padding: 32px 0;
      flex: 1;
      @media (min-width: 768px) {
        padding: 112px 0 24px;
      }
      .top {
        text-align: center;
        .header {
          height: 44px;
          line-height: 44px;
          a {
            text-decoration: none;
          }
          .logo {
            height: 44px;
            vertical-align: top;
            margin-right: 16px;
          }
          .title {
            font-size: 33px;
            color: rgba(0, 0, 0, .85);
            font-family: 'Myriad Pro', 'Helvetica Neue', Arial, Helvetica, sans-serif;
            font-weight: 600;
            position: relative;
            top: 2px;
          }
        }
        .desc {
          font-size: 14px;
          color: rgba(0, 0, 0, .45);
          margin-top: 12px;
          margin-bottom: 40px;
        }
      }
      .login {
        width: 368px;
        margin: 0 auto;
        @media screen and (max-width: 576px) {
          width:95%;
        }
        @media screen and (max-width: 320px) {
          .captcha-button {
            font-size: 14px;
          }
        }
        .icon {
          font-size: 24px;
          color: rgba(0, 0, 0, 0.2);
          margin-left: 16px;
          vertical-align: middle;
          cursor: pointer;
          transition: color 0.3s;
          & :hover {
            color: #1890ff;
          }
        }
      }
    }
  }
</style>



